@import "variables";
@import "mixins";
@import "fonts";
@import "global";
@import "animation";

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

*:focus:not(:focus-visible) {
  outline: none;
}

:root {
  --offset-page: 30px;
  --header-height: 84px;

  @media (max-width: $viewport-m) {
    --header-height: 68px;
  }

  @media (max-width: $viewport-s) {
    --offset-page: 16px;
    --header-height: 56px;
  }
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.wrapper {
  max-width: calc(1140px + var(--offset-page) * 2);
  width: 100%;
  margin: 0 auto;
  padding: 0 var(--offset-page);
}

.content-img {
  height: 100%;
  object-fit: cover;
  object-position: 50% 0;
  //background-color: $bg-img;
}

.content {
  position: relative;
  max-width: 100%;
  transition: max-width $delay-default;
}

.btn {
  padding: 16px 32px;
  flex-shrink: 0;
  font-weight: 600;
  color: white;
  border: 2px solid $color-blue;

  @media (max-width: $viewport-m) {
    padding: 10px 22px;
  }

  @media (max-width: $viewport-s) {
    width: 100%;
    max-width: 350px;
    margin: 0 auto;
  }

  &--blue {
    background: $color-blue;
    transition: background-color $delay-default, border-color $delay-default;

    &:hover,
    &:focus {
      border-color: $color-blue-hover;
      background: $color-blue-hover;
    }
  }

  &:active {
    //transform: scale3d(.99, .99, 1);
  }
}

.title {
  position: relative;
  padding-left: 28px;
  font-weight: 600;
  font-size: 40px;
  line-height: 1.3;
  color: $font-black;

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 4px;
    height: 52px;
    background: $color-blue;
  }

  @media (max-width: $viewport-s) {
    font-size: 28px;
    padding-left: 0;

    &::before {
      content: none;
    }
  }
}

.block-offset {
  padding-top: 160px;
  padding-bottom: 160px;

  @media (max-width: $viewport-m) {
    padding-top: 100px;
    padding-bottom: 100px;
  }

  @media (max-width: $viewport-s) {
    padding-top: 72px;
    padding-bottom: 72px;
  }
}
