.application {
  background: #F4F6FA;
}

.application__wrap {
  display: flex;
  max-width: 660px;
  flex-direction: column;
  align-items: center;
  margin: 0 auto;
}

.application__title {
  margin-bottom: 16px;
  font-weight: 600;
  font-size: 40px;
  line-height: 1.3;
  color: $font-black;
}

.application__text {
  max-width: 354px;
  font-size: 20px;
  color: $color-gray;
  text-align: center;
}

.application__form {
  display: flex;
  width: 100%;
}

.application__label {
  flex-grow: 1;

  input {
    width: 100%;
    border-color: transparent;
    background: white;
  }
}

.application__agreement {
  font-size: 14px;
  line-height: 1;
  color: #B6B6B6;

  a {
    color: $color-blue;
    text-decoration: underline;
  }
}
